<template>
  <div>
    <base-table-panel
      title="岗位管理"
      class="sortTable noTableBorder"
      :right-btns="titleBtns()"
      :data-source="tableData"
      :loading="loading"
      :columns="columns()"
      :show-summary="false"
      :total="total"
      has-pagination
      :current-page="page"
      :page-size="limit"
      :border-style="true"
      @handleCurrent="handleCurrentChange"
      @handleSize="handlePageSizeChange"
    >
      <template slot="operate-left">
        <el-form
          @submit.native.prevent
          :inline="true"
          label-suffix=":"
          size="mini"
          class="operate-left-form"
        >
          <el-form-item>
            <el-input
              v-model="inputSearch"
              placeholder="请输入要搜索的内容"
              suffix-icon="el-icon-search"
              @keyup.enter.native="keySearch"
              @blur="keySearch"
              clearable
            >
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="keySearch">查询</el-button>
          </el-form-item>
        </el-form>
      </template>
    </base-table-panel>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import paramsCache from '@mixins/paramCache';
import { postListQuery, postDel, postListAsync } from '@api/authority';

const paramsCacheMixin = paramsCache([
  'tableData',
  'page',
  'limit',
  'total',
  'inputSearch'
]);
export default {
  name: 'UserGroupManage',
  mixins: [paramsCacheMixin],
  data() {
    return {
      inputSearch: '',
      page: 1,
      limit: 10,
      total: null,
      tableData: [],
      loading: false,
      users: {},
      titleBtns() {
        const _this = this;
        return [
          {
            title: '手工同步',
            icon: 'refresh-line',
            code: 'sync',
            onClick: _this.handleLoad
          },
          {
            title: '新增岗位',
            icon: 'add-line',
            code: 'add',
            onClick: _this.addUserGroup
          }
        ];
      },
      columns() {
        const _this = this;
        const natureTrans = {
          '001': '员工岗',
          '002': '业务组长岗',
          '003': '业务主管岗',
          '004': '部门行政负责人',
          '005': '系统管理员'
        };
        return [
          {
            prop: 'name',
            label: '岗位名称',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '200'
          },
          {
            prop: 'groupNature',
            label: '岗位性质',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '200',
            render: (h, record) => {
              return h('span', natureTrans[record.record.groupNature]);
            }
          },
          {
            prop: 'parentName',
            label: '上级岗位',
            'header-align': 'left',
            showOverflowTooltip: true,
            'min-width': '200'
          },
          {
            prop: '',
            label: '操作',
            align: 'center',
            width: '140',
            actions: [
              {
                code: 'edit',
                text: '编辑',
                onClick: _this.editUserGroup
              },
              {
                code: 'del',
                text: '删除',
                onClick: _this.del
              }
            ]
          }
        ];
      }
    };
  },

  created() {
    this.freshManagement
      ? this.getStationList(1)
      : !this.tableData.length && this.getStationList();
  },
  computed: {
    ...mapGetters(['freshManagement'])
  },
  beforeRouteLeave(to, from, next) {
    this.cacheParamData();
    next();
  },
  methods: {
    // 岗位同步
    handleLoad() {
      this.$confirm('确定要同步岗位信息到本系统吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        postListAsync()
          .then(({ error }) => {
            if (!error) {
              this.$notify.success({
                title: '操作成功',
                message: '岗位信息刷新成功'
              });
              this.getStationList();
            } else {
              this.$notify.error({
                title: '操作失败',
                message: error.message
              });
            }
          })
          .catch((err) => {
            this.$message.error('同步岗位信息失败！');
            console.log(err);
          });
      });
    },
    // 模糊查询
    keySearch() {
      this.page = 1;
      this.getStationList();
    },
    // 岗位列表查询
    getStationList(page) {
      page ? (this.page = 1) : '';
      this.loading = true;
      const params = {
        page: this.page,
        limit: this.limit
      };
      if (this.inputSearch && this.inputSearch !== '') {
        params.name = this.inputSearch;
      }
      postListQuery(params).then(({ data }) => {
        this.loading = false;
        if (data) {
          this.tableData = data.rows;
          this.total = data.total;
        }
      });
    },
    handleCurrentChange(val) {
      this.page = val;
      this.getStationList();
    },
    handlePageSizeChange(val) {
      this.limit = val;
      this.page = 1;
      this.getStationList();
    },
    // 删除岗位
    del(row) {
      this.$confirm('确定要删除该岗位?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        postDel(row.id).then(({ error }) => {
          if (!error) {
            this.$message.success('岗位删除成功');
            this.getStationList();
          } else {
            this.$message.error(error.message);
          }
        });
      });
    },
    // 回车
    changeMananement() {
      this.page = 1;
      this.getStationList();
    },
    // 新增岗位
    addUserGroup() {
      this.routerToDetail('0');
    },
    // 修改
    editUserGroup(config) {
      this.routerToDetail('1', config.id);
    },
    // 新增、修改岗位
    routerToDetail(type, id) {
      this.cacheParamData();
      this.$router.push({ name: 'postDetail', query: { type, id } });
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss">
@import '~@assets/scss/theme.scss';

.user-group-manager {
  .head {
    width: 100%;
    height: 50px;
    box-sizing: border-box;
    position: absolute;
    top: 0;
    left: 170px;
    .sub-tabs-card {
      width: calc(100% - 200px);
      margin-top: 19px;
    }
  }
  &-title {
    margin-top: 20px;
    margin-bottom: 10px;

    .search {
      float: left;
      width: 240px;
    }

    .addGroup-btn {
      float: right;
      height: 32px;
      padding: 0 15px;
      .icon {
        width: 12px;
        height: 12px;
        margin-right: 6px;
        color: #b5b5b5;
      }
      &:hover {
        .icon {
          color: #2a76cd;
        }
      }
    }
  }
  .text-btn {
    color: #2a76cd;
    font-size: 14px;
    cursor: pointer;
    margin-left: 20px;
    &:first-child {
      margin-left: 0;
    }
  }
  .pagination {
    margin-top: 20px;
  }

  .table-botton {
    overflow: hidden;
    .add-user-group {
      float: right;
    }
  }
}
</style>
